//Configuration

//Basevalue
$rfs-base-value:1.25rem!default;
$rfs-unit:rem!default;

@if$rfs-unit!=remand$rfs-unit!=px{
@error"`#{$rfs-unit}`isnotavalidunitfor$rfs-unit.Use`px`or`rem`.";
}

//Breakpointatwherevaluesstartdecreasingifscreenwidthissmaller
$rfs-breakpoint:1200px!default;
$rfs-breakpoint-unit:px!default;

@if$rfs-breakpoint-unit!=pxand$rfs-breakpoint-unit!=emand$rfs-breakpoint-unit!=rem{
@error"`#{$rfs-breakpoint-unit}`isnotavalidunitfor$rfs-breakpoint-unit.Use`px`,`em`or`rem`.";
}

//Resizevaluesbasedonscreenheightandwidth
$rfs-two-dimensional:false!default;

//Factorofdecrease
$rfs-factor:10!default;

@iftype-of($rfs-factor)!=numberor$rfs-factor<=1{
@error"`#{$rfs-factor}`isnotavalid$rfs-factor,itmustbegreaterthan1.";
}

//Mode.Possibilities:"min-media-query","max-media-query"
$rfs-mode:min-media-query!default;

//Generateenableordisableclasses.Possibilities:false,"enable"or"disable"
$rfs-class:false!default;

//1rem=$rfs-rem-valuepx
$rfs-rem-value:16!default;

//Safariiframeresizebug:https://github.com/twbs/rfs/issues/14
$rfs-safari-iframe-resize-bug-fix:false!default;

//DisableRFSbysetting$enable-rfstofalse
$enable-rfs:true!default;

//Cache$rfs-base-valueunit
$rfs-base-value-unit:unit($rfs-base-value);

@functiondivide($dividend,$divisor,$precision:10){
$sign:if($dividend>0and$divisor>0or$dividend<0and$divisor<0,1,-1);
$dividend:abs($dividend);
$divisor:abs($divisor);
@if$dividend==0{
@return0;
}
@if$divisor==0{
@error"Cannotdivideby0";
}
$remainder:$dividend;
$result:0;
$factor:10;
@while($remainder>0and$precision>=0){
$quotient:0;
@while($remainder>=$divisor){
$remainder:$remainder-$divisor;
$quotient:$quotient+1;
}
$result:$result*10+$quotient;
$factor:$factor*.1;
$remainder:$remainder*10;
$precision:$precision-1;
@if($precision<0and$remainder>=$divisor*5){
$result:$result+1;
}
}
$result:$result*$factor*$sign;
$dividend-unit:unit($dividend);
$divisor-unit:unit($divisor);
$unit-map:(
"px":1px,
"rem":1rem,
"em":1em,
"%":1%
);
@if($dividend-unit!=$divisor-unitandmap-has-key($unit-map,$dividend-unit)){
$result:$result*map-get($unit-map,$dividend-unit);
}
@return$result;
}

//Removepx-unitfrom$rfs-base-valueforcalculations
@if$rfs-base-value-unit==px{
$rfs-base-value:divide($rfs-base-value,$rfs-base-value*0+1);
}
@elseif$rfs-base-value-unit==rem{
$rfs-base-value:divide($rfs-base-value,divide($rfs-base-value*0+1,$rfs-rem-value));
}

//Cache$rfs-breakpointunittopreventmultiplecalls
$rfs-breakpoint-unit-cache:unit($rfs-breakpoint);

//Removeunitfrom$rfs-breakpointforcalculations
@if$rfs-breakpoint-unit-cache==px{
$rfs-breakpoint:divide($rfs-breakpoint,$rfs-breakpoint*0+1);
}
@elseif$rfs-breakpoint-unit-cache==remor$rfs-breakpoint-unit-cache=="em"{
$rfs-breakpoint:divide($rfs-breakpoint,divide($rfs-breakpoint*0+1,$rfs-rem-value));
}

//Calculatethemediaqueryvalue
$rfs-mq-value:if($rfs-breakpoint-unit==px,#{$rfs-breakpoint}px,#{divide($rfs-breakpoint,$rfs-rem-value)}#{$rfs-breakpoint-unit});
$rfs-mq-property-width:if($rfs-mode==max-media-query,max-width,min-width);
$rfs-mq-property-height:if($rfs-mode==max-media-query,max-height,min-height);

//Internalmixinusedtodeterminewhichmediaqueryneedstobeused
@mixin_rfs-media-query{
@if$rfs-two-dimensional{
@if$rfs-mode==max-media-query{
@media(#{$rfs-mq-property-width}:#{$rfs-mq-value}),(#{$rfs-mq-property-height}:#{$rfs-mq-value}){
@content;
}
}
@else{
@media(#{$rfs-mq-property-width}:#{$rfs-mq-value})and(#{$rfs-mq-property-height}:#{$rfs-mq-value}){
@content;
}
}
}
@else{
@media(#{$rfs-mq-property-width}:#{$rfs-mq-value}){
@content;
}
}
}

//Internalmixinthataddsdisableclassestotheselectorifneeded.
@mixin_rfs-rule{
@if$rfs-class==disableand$rfs-mode==max-media-query{
//Addinganextraclassincreasesspecificity,whichpreventsthemediaquerytooverridetheproperty
&,
.disable-rfs&,
&.disable-rfs{
@content;
}
}
@elseif$rfs-class==enableand$rfs-mode==min-media-query{
.enable-rfs&,
&.enable-rfs{
@content;
}
}
@else{
@content;
}
}

//Internalmixinthataddsenableclassestotheselectorifneeded.
@mixin_rfs-media-query-rule{

@if$rfs-class==enable{
@if$rfs-mode==min-media-query{
@content;
}

@include_rfs-media-query{
.enable-rfs&,
&.enable-rfs{
@content;
}
}
}
@else{
@if$rfs-class==disableand$rfs-mode==min-media-query{
.disable-rfs&,
&.disable-rfs{
@content;
}
}
@include_rfs-media-query{
@content;
}
}
}

//Helperfunctiontogettheformattednon-responsivevalue
@functionrfs-value($values){
//Converttolist
$values:if(type-of($values)!=list,($values,),$values);

$val:'';

//Loopovereachvalueandcalculatevalue
@each$valuein$values{
@if$value==0{
$val:$val+'0';
}
@else{
//Cache$valueunit
$unit:if(type-of($value)=="number",unit($value),false);

@if$unit==px{
//Converttoremifneeded
$val:$val+''+if($rfs-unit==rem,#{divide($value,$value*0+$rfs-rem-value)}rem,$value);
}
@elseif$unit==rem{
//Converttopxifneeded
$val:$val+''+if($rfs-unit==px,#{divide($value,$value*0+1)*$rfs-rem-value}px,$value);
}
@else{
//If$valueisn'tanumber(likeinherit)or$valuehasaunit(notpxorrem,like1.5em)or$is0,justprintthevalue
$val:$val+''+$value;
}
}
}

//Removefirstspace
@returnunquote(str-slice($val,2));
}

//HelperfunctiontogettheresponsivevaluecalculatedbyRFS
@functionrfs-fluid-value($values){
//Converttolist
$values:if(type-of($values)!=list,($values,),$values);

$val:'';

//Loopovereachvalueandcalculatevalue
@each$valuein$values{
@if$value==0{
$val:$val+'0';
}

@else{
//Cache$valueunit
$unit:if(type-of($value)=="number",unit($value),false);

//If$valueisn'tanumber(likeinherit)or$valuehasaunit(notpxorrem,like1.5em)or$is0,justprintthevalue
@ifnot$unitor$unit!=pxand$unit!=rem{
$val:$val+''+$value;
}

@else{
//Removeunitfrom$valueforcalculations
$value:divide($value,$value*0+if($unit==px,1,divide(1,$rfs-rem-value)));

//Onlyaddthemediaqueryifthevalueisgreaterthantheminimumvalue
@ifabs($value)<=$rfs-base-valueornot$enable-rfs{
$val:$val+''+if($rfs-unit==rem,#{divide($value,$rfs-rem-value)}rem,#{$value}px);
}
@else{
//Calculatetheminimumvalue
$value-min:$rfs-base-value+divide(abs($value)-$rfs-base-value,$rfs-factor);

//Calculatedifferencebetween$valueandtheminimumvalue
$value-diff:abs($value)-$value-min;

//Basevalueformatting
$min-width:if($rfs-unit==rem,#{divide($value-min,$rfs-rem-value)}rem,#{$value-min}px);

//Usenegativevalueifneeded
$min-width:if($value<0,-$min-width,$min-width);

//Use`vmin`iftwo-dimensionalisenabled
$variable-unit:if($rfs-two-dimensional,vmin,vw);

//Calculatethevariablewidthbetween0and$rfs-breakpoint
$variable-width:#{divide($value-diff*100,$rfs-breakpoint)}#{$variable-unit};

//Returnthecalculatedvalue
$val:$val+'calc('+$min-width+if($value<0,'-','+')+$variable-width+')';
}
}
}
}

//Removefirstspace
@returnunquote(str-slice($val,2));
}

//RFSmixin
@mixinrfs($values,$property:font-size){
@if$values!=null{
$val:rfs-value($values);
$fluidVal:rfs-fluid-value($values);

//Donotprintthemediaqueryifresponsive&non-responsivevaluesarethesame
@if$val==$fluidVal{
#{$property}:$val;
}
@else{
@include_rfs-rule{
#{$property}:if($rfs-mode==max-media-query,$val,$fluidVal);

//Includesafariiframeresizefixifneeded
min-width:if($rfs-safari-iframe-resize-bug-fix,(0*1vw),null);
}

@include_rfs-media-query-rule{
#{$property}:if($rfs-mode==max-media-query,$fluidVal,$val);
}
}
}
}

//Shorthandhelpermixins
@mixinfont-size($value){
@includerfs($value);
}

@mixinpadding($value){
@includerfs($value,padding);
}

@mixinpadding-top($value){
@includerfs($value,padding-top);
}

@mixinpadding-right($value){
@includerfs($value,padding-right);
}

@mixinpadding-bottom($value){
@includerfs($value,padding-bottom);
}

@mixinpadding-left($value){
@includerfs($value,padding-left);
}

@mixinmargin($value){
@includerfs($value,margin);
}

@mixinmargin-top($value){
@includerfs($value,margin-top);
}

@mixinmargin-right($value){
@includerfs($value,margin-right);
}

@mixinmargin-bottom($value){
@includerfs($value,margin-bottom);
}

@mixinmargin-left($value){
@includerfs($value,margin-left);
}
