//stylelint-disabledeclaration-no-important,selector-no-qualifying-type,property-no-vendor-prefix


//Reboot
//
//NormalizationofHTMLelements,manuallyforkedfromNormalize.csstoremove
//stylestargetingirrelevantbrowserswhileapplyingnewstyles.
//
//NormalizeislicensedMIT.https://github.com/necolas/normalize.css


//Document
//
//Changefrom`box-sizing:content-box`sothat`width`isnotaffectedby`padding`or`border`.

*,
*::before,
*::after{
box-sizing:border-box;
}


//Root
//
//Abilitytothevalueoftherootfontsizes,affectingthevalueof`rem`.
//nullbydefault,thusnothingisgenerated.

:root{
@if$font-size-root!=null{
font-size:var(--#{$variable-prefix}-root-font-size);
}

@if$enable-smooth-scroll{
@media(prefers-reduced-motion:no-preference){
scroll-behavior:smooth;
}
}
}


//Body
//
//1.Removethemargininallbrowsers.
//2.Asabestpractice,applyadefault`background-color`.
//3.PreventadjustmentsoffontsizeafterorientationchangesiniOS.
//4.ChangethedefaulttaphighlighttobecompletelytransparentiniOS.

//scss-docs-startreboot-body-rules
body{
margin:0;//1
font-family:var(--#{$variable-prefix}body-font-family);
@includefont-size(var(--#{$variable-prefix}body-font-size));
font-weight:var(--#{$variable-prefix}body-font-weight);
line-height:var(--#{$variable-prefix}body-line-height);
color:var(--#{$variable-prefix}body-color);
text-align:var(--#{$variable-prefix}body-text-align);
background-color:var(--#{$variable-prefix}body-bg);//2
-webkit-text-size-adjust:100%;//3
-webkit-tap-highlight-color:rgba($black,0);//4
}
//scss-docs-endreboot-body-rules


//Contentgrouping
//
//1.ResetFirefox'sgraycolor
//2.Setcorrectheightandpreventthe`size`attributetomakethe`hr`looklikeaninputfield

hr{
margin:$hr-margin-y0;
color:$hr-color;//1
background-color:currentColor;
border:0;
opacity:$hr-opacity;
}

hr:not([size]){
height:$hr-height;//2
}


//Typography
//
//1.Removetopmarginsfromheadings
//Bydefault,`<h1>`-`<h6>`allreceivetopandbottommargins.Wenukethetop
//marginforeasiercontrolwithintypescalesasitavoidsmargincollapsing.

%heading{
margin-top:0;//1
margin-bottom:$headings-margin-bottom;
font-family:$headings-font-family;
font-style:$headings-font-style;
font-weight:$headings-font-weight;
line-height:$headings-line-height;
color:$headings-color;
}

h1{
@extend%heading;
@includefont-size($h1-font-size);
}

h2{
@extend%heading;
@includefont-size($h2-font-size);
}

h3{
@extend%heading;
@includefont-size($h3-font-size);
}

h4{
@extend%heading;
@includefont-size($h4-font-size);
}

h5{
@extend%heading;
@includefont-size($h5-font-size);
}

h6{
@extend%heading;
@includefont-size($h6-font-size);
}


//Resetmarginsonparagraphs
//
//Similarly,thetopmarginon`<p>`sgetreset.However,wealsoresetthe
//bottommargintouse`rem`unitsinsteadof`em`.

p{
margin-top:0;
margin-bottom:$paragraph-margin-bottom;
}


//Abbreviations
//
//1.Duplicatebehaviortothedata-bs-*attributeforourtooltipplugin
//2.AddthecorrecttextdecorationinChrome,Edge,Opera,andSafari.
//3.Addexplicitcursortoindicatechangedbehavior.
//4.Preventthetext-decorationtobeskipped.

abbr[title],
abbr[data-bs-original-title]{//1
text-decoration:underlinedotted;//2
cursor:help;//3
text-decoration-skip-ink:none;//4
}


//Address

address{
margin-bottom:1rem;
font-style:normal;
line-height:inherit;
}


//Lists

ol,
ul{
padding-left:2rem;
}

ol,
ul,
dl{
margin-top:0;
margin-bottom:1rem;
}

olol,
ulul,
olul,
ulol{
margin-bottom:0;
}

dt{
font-weight:$dt-font-weight;
}

//1.Undobrowserdefault

dd{
margin-bottom:.5rem;
margin-left:0;//1
}


//Blockquote

blockquote{
margin:001rem;
}


//Strong
//
//AddthecorrectfontweightinChrome,Edge,andSafari

b,
strong{
font-weight:$font-weight-bolder;
}


//Small
//
//Addthecorrectfontsizeinallbrowsers

small{
@includefont-size($small-font-size);
}


//Mark

mark{
padding:$mark-padding;
background-color:$mark-bg;
}


//SubandSup
//
//Prevent`sub`and`sup`elementsfromaffectingthelineheightin
//allbrowsers.

sub,
sup{
position:relative;
@includefont-size($sub-sup-font-size);
line-height:0;
vertical-align:baseline;
}

sub{bottom:-.25em;}
sup{top:-.5em;}


//Links

a{
color:$link-color;
text-decoration:$link-decoration;

&:hover{
color:$link-hover-color;
text-decoration:$link-hover-decoration;
}
}

//Andundothesestylesforplaceholderlinks/namedanchors(withouthref).
//Itwouldbemorestraightforwardtojustusea[href]inpreviousblock,butthat
//causesspecificityissuesinmanyotherstylesthataretoocomplextofix.
//Seehttps://github.com/twbs/bootstrap/issues/19402

a:not([href]):not([class]){
&,
&:hover{
color:inherit;
text-decoration:none;
}
}


//Code

pre,
code,
kbd,
samp{
font-family:$font-family-code;
@includefont-size(1em);//Correcttheodd`em`fontsizinginallbrowsers.
direction:ltr#{"/*rtl:ignore*/"};
unicode-bidi:bidi-override;
}

//1.Removebrowserdefaulttopmargin
//2.Resetbrowserdefaultof`1em`touse`rem`s
//3.Don'tallowcontenttobreakoutside

pre{
display:block;
margin-top:0;//1
margin-bottom:1rem;//2
overflow:auto;//3
@includefont-size($code-font-size);
color:$pre-color;

//Accountforsomecodeoutputsthatplacecodetagsinpretags
code{
@includefont-size(inherit);
color:inherit;
word-break:normal;
}
}

code{
@includefont-size($code-font-size);
color:$code-color;
word-wrap:break-word;

//Streamlinethestylewheninsideanchorstoavoidbrokenunderlineandmore
a>&{
color:inherit;
}
}

kbd{
padding:$kbd-padding-y$kbd-padding-x;
@includefont-size($kbd-font-size);
color:$kbd-color;
background-color:$kbd-bg;
@includeborder-radius($border-radius-sm);

kbd{
padding:0;
@includefont-size(1em);
font-weight:$nested-kbd-font-weight;
}
}


//Figures
//
//Applyaconsistentmarginstrategy(matchesourtypestyles).

figure{
margin:001rem;
}


//Imagesandcontent

img,
svg{
vertical-align:middle;
}


//Tables
//
//Preventdoubleborders

table{
caption-side:bottom;
border-collapse:collapse;
}

caption{
padding-top:$table-cell-padding-y;
padding-bottom:$table-cell-padding-y;
color:$table-caption-color;
text-align:left;
}

//1.Removesfont-weightboldbyinheriting
//2.Matchesdefault`<td>`alignmentbyinheriting`text-align`.
//3.FixalignmentforSafari

th{
font-weight:$table-th-font-weight;//1
text-align:inherit;//2
text-align:-webkit-match-parent;//3
}

thead,
tbody,
tfoot,
tr,
td,
th{
border-color:inherit;
border-style:solid;
border-width:0;
}


//Forms
//
//1.Allowlabelstouse`margin`forspacing.

label{
display:inline-block;//1
}

//Removethedefault`border-radius`thatmacOSChromeadds.
//Seehttps://github.com/twbs/bootstrap/issues/24093

button{
//stylelint-disable-next-lineproperty-disallowed-list
border-radius:0;
}

//ExplicitlyremovefocusoutlineinChromiumwhenitshouldn'tbe
//visible(e.g.asresultofmouseclickortouchtap).Italready
//shouldbedoingthisautomatically,butseemstocurrentlybe
//confusedandappliesitsveryvisibletwo-toneoutlineanyway.

button:focus:not(:focus-visible){
outline:0;
}

//1.RemovethemargininFirefoxandSafari

input,
button,
select,
optgroup,
textarea{
margin:0;//1
font-family:inherit;
@includefont-size(inherit);
line-height:inherit;
}

//RemovetheinheritanceoftexttransforminFirefox
button,
select{
text-transform:none;
}
//Setthecursorfornon-`<button>`buttons
//
//Detailsathttps://github.com/twbs/bootstrap/pull/30562
[role="button"]{
cursor:pointer;
}

select{
//Removetheinheritanceofword-wrapinSafari.
//Seehttps://github.com/twbs/bootstrap/issues/24990
word-wrap:normal;

//UndotheopacitychangefromChrome
&:disabled{
opacity:1;
}
}

//RemovethedropdownarrowinChromefrominputsbuiltwithdatalists.
//Seehttps://stackoverflow.com/a/54997118

[list]::-webkit-calendar-picker-indicator{
display:none;
}

//1.PreventaWebKitbugwhere(2)destroysnative`audio`and`video`
//controlsinAndroid4.
//2.CorrecttheinabilitytostyleclickabletypesiniOSandSafari.
//3.Opinionated:add"hand"cursortonon-disabledbuttonelements.

button,
[type="button"],//1
[type="reset"],
[type="submit"]{
-webkit-appearance:button;//2

@if$enable-button-pointers{
&:not(:disabled){
cursor:pointer;//3
}
}
}

//RemoveinnerborderandpaddingfromFirefox,butdon'trestoretheoutlinelikeNormalize.

::-moz-focus-inner{
padding:0;
border-style:none;
}

//1.Textareasshouldreallyonlyresizeverticallysotheydon'tbreaktheir(horizontal)containers.

textarea{
resize:vertical;//1
}

//1.Browserssetadefault`min-width:min-content;`onfieldsets,
//unlikee.g.`<div>`s,whichhave`min-width:0;`bydefault.
//Soweresetthattoensurefieldsetsbehavemorelikeastandardblockelement.
//Seehttps://github.com/twbs/bootstrap/issues/12359
//andhttps://html.spec.whatwg.org/multipage/#the-fieldset-and-legend-elements
//2.Resetthedefaultoutlinebehavioroffieldsetssotheydon'taffectpagelayout.

fieldset{
min-width:0;//1
padding:0;//2
margin:0;//2
border:0;//2
}

//1.Byusing`float:left`,thelegendwillbehavelikeablockelement.
//Thiswaytheborderofafieldsetwrapsaroundthelegendifpresent.
//2.Fixwrappingbug.
//Seehttps://github.com/twbs/bootstrap/issues/29712

legend{
float:left;//1
width:100%;
padding:0;
margin-bottom:$legend-margin-bottom;
@includefont-size($legend-font-size);
font-weight:$legend-font-weight;
line-height:inherit;

+*{
clear:left;//2
}
}

//Fixheightofinputswithatypeofdatetime-local,date,month,week,ortime
//Seehttps://github.com/twbs/bootstrap/issues/18842

::-webkit-datetime-edit-fields-wrapper,
::-webkit-datetime-edit-text,
::-webkit-datetime-edit-minute,
::-webkit-datetime-edit-hour-field,
::-webkit-datetime-edit-day-field,
::-webkit-datetime-edit-month-field,
::-webkit-datetime-edit-year-field{
padding:0;
}

::-webkit-inner-spin-button{
height:auto;
}

//1.CorrecttheoutlinestyleinSafari.
//2.ThisoverridestheextraroundedcornersonsearchinputsiniOSsothatour
//`.form-control`classcanproperlystylethem.Notethatthiscannotsimply
//beaddedto`.form-control`asit'snotspecificenough.Fordetails,see
//https://github.com/twbs/bootstrap/issues/11586.

[type="search"]{
outline-offset:-2px;//1
-webkit-appearance:textfield;//2
}

//1.AfewinputtypesshouldstayLTR
//Seehttps://rtlstyling.com/posts/rtl-styling#form-inputs
//2.RTLonlyoutput
//Seehttps://rtlcss.com/learn/usage-guide/control-directives/#raw

/*rtl:raw:
[type="tel"],
[type="url"],
[type="email"],
[type="number"]{
direction:ltr;
}
*/

//RemovetheinnerpaddinginChromeandSafarionmacOS.

::-webkit-search-decoration{
-webkit-appearance:none;
}

//Removepaddingaroundcolorpickersinwebkitbrowsers

::-webkit-color-swatch-wrapper{
padding:0;
}


//Inheritfontfamilyandlineheightforfileinputbuttons

::file-selector-button{
font:inherit;
}

//1.Changefontpropertiesto`inherit`
//2.CorrecttheinabilitytostyleclickabletypesiniOSandSafari.

::-webkit-file-upload-button{
font:inherit;//1
-webkit-appearance:button;//2
}

//Correctelementdisplays

output{
display:inline-block;
}

//Removeborderfromiframe

iframe{
border:0;
}

//Summary
//
//1.Addthecorrectdisplayinallbrowsers

summary{
display:list-item;//1
cursor:pointer;
}


//Progress
//
//AddthecorrectverticalalignmentinChrome,Firefox,andOpera.

progress{
vertical-align:baseline;
}


//Hiddenattribute
//
//Alwayshideanelementwiththe`hidden`HTMLattribute.

[hidden]{
display:none!important;
}
